*{
  margin:0;
  padding:0;
}

.my-app{
  height:400px;
  width:400px;
  margin:auto;
  display:flex;
  align-items: center;
  flex-direction: column;
  box-shadow:1px 5px 5px 1px;
  padding:10px 0 10px 0;
  background-color: white;
}

.container{
  width:400px;
  border:1px solid black;
}
.calculator{
  display:grid;
  grid-template-columns:repeat(4,100px);
}

button.operator{
  background-color:lightgrey;
}
button{
  height:50px;
}
.display{
  width:400px;
  display:grid;
  grid-column-start:1;
  grid-column-end:5;
  text-align:right;
  font-size:50px;
  
}

.txt{
  height:20px;
  font-size:20px;
  text-align: left;
 }
  @media screen and (min-width:600px) and (min-height:600px){
    .my-app{
      width:50vw;
      height:50vh;
    }
    .calculator{
      grid-template-columns:repeat(4,100px);
    }
    .container{
      width:400px;
    }
     button{
      height:50px;
    }
    .display{
      width:25vw;
    }
    .txt{
      height:20px;
      font-size: 20px;
    }
    
  }